import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lorium',
  templateUrl: './lorium.component.html',
  styleUrls: ['./lorium.component.css']
})
export class LoriumComponent implements OnInit {

   title = 'app';
   cls = 'colorR';
   cls1 = 'colorG';
   TaskList = [];
   task = '';
   counterTask = ''; 
   fcolor = '';
   show: boolean = true;
   colors = [this.cls , this.cls1];
  //  use object insted Array
  color1 = { colorG: true, colorR: true};
  
size:number = 25

   inlineColor = {
    'font-size': '25px',
    'color' : 'blue',
    'font-weight': 'bold'
   };

   showView: number = 0;
  constructor() { }

  ngOnInit() {
  }
   
  addTask(){
    if(this.task !=''){
      this.TaskList.push(this.task);
      this.counterTask = this.task;
      this.task='';
    }
  }

    removeTask(index){
      this.TaskList.splice(index,1);
    }
    changbutton(){
    this.show = !this.show;
    }
    changeColor (val){
      this.fcolor = val;
    }
  }

